    /*padding: .3em 0 .2em 1em;*/
    border-bottom: 3px solid #3464ff;
    color: #333333;
    width: 500px;
    margin-left: auto;
    margin-right: auto;
}
.heading[data-v-3ccc41e3]::before {
    position: absolute;
    top: 0;
    left: .3em;
    -webkit-transform: rotate(55deg);
            transform: rotate(55deg);
    height: 11px;
    width: 12px;
    background: #3464ff;
    content: '';
}
.heading[data-v-3ccc41e3]::after {
    position: absolute;
    -webkit-transform: rotate(15deg);
            transform: rotate(15deg);
    top: .6em;
    left: 0;
    height: 8px;
    width: 8px;
    background: #3464ff;
    content: '';
}
